import styled from 'styled-components';
var Styled = {};

Styled.Top = styled.div`
        position:relative;
        width:100%;
        height:1.5rem;
        color:var(--app-colorOnDark3);
        display:flex;
        z-index:90;
        padding-top: 1.75rem;
        padding-bottom: 0.5rem;
        pointer-events: none;
        @media (max-width:750px) {
            padding-top:1.25rem;
            padding-bottom:0.8rem;
            height:2rem;
            > div {
                position:fixed;
                width:100%;
                display:flex;
            }
        }
        &.notifications {
            position:absolute;
            z-index:99;
            padding-bottom:0;
            @media (max-width:750px) {
                position:relative;
                * {
                    text-align:center;
                }
            }
        }
        > * {
            position:relative;
            display:inline-block;
            pointer-events: auto;
            flex-grow:1;
        }
`;

Styled.Logo = styled.div`
        margin-left:1rem;
        display:inline-block;
        cursor:pointer;
        position: absolute;
        width: 100%;
        img {
            height:2.2rem;
            margin-top:-0.5rem;
        }
        .menu {
            display:none;
        }
        @media (max-width:750px) {
            display:block;
            margin:0 auto;
            text-align:center;
        }
        .icon-more {
            color:var(--app-colorGreen1);
        }
`;

Styled.Details = styled.div`   
        position:absolute;
        right:0;
        display:inline-block;
        cursor:pointer;
        text-align:right;
        margin-right:2.5px;
        margin-top:0.25rem;
        .link {
            display:inline-block;
            @media (max-width:750px) {
                display:none;
            }
            .title {
                font-size:1.5rem;
                line-height:1.25rem;
                color:var(--app-colorOnDark2);
                display:block;
                padding:0 0 0 0;
                margin-top:-0.4rem;
                white-space:nowrap;
            }
            .text {
                font-size:1rem;
                line-height:0.85rem;
                color:var(--app-colorOnDark3);
            }
        }
        .fontIcon {
            position:absolute;
            right:0;
            color:#fff;
            font-size: 1.7rem;
            margin: -0.25rem -0.45rem;
            @media (min-width:751px) {
                display:none;
            }
        }
`;

Styled.Hamburger = styled.div`
        position:relative;
        z-index:10;
        padding:0 0 0 1rem;
        display: inline-block;
        cursor: pointer;
        font: inherit;
        color: inherit;
        text-transform: none;
        background-color: transparent;
        border: 0;
        margin: 0;
        overflow: visible;
        transition: all 0.5s;
        @media (min-width:751px) {
            display:none;
        }
        .hamburger-icon {
            position: relative; 
            width: 32px;
            height: 28px;
            display: inline-block;
        }
        .hamburger-centered {
            position:absolute;
            display: block;
            top: 50%;
        }
        /*
            each line
        */
        .hamburger-middle, 
        .hamburger-top, 
        .hamburger-bottom {
            position: absolute;
            display:inline-block;
            margin-top:-4px;
            width: 31px;
            height: 4px;
            background-color: #fff;
            transform: translate3d(0, 0, 0) rotate(0deg) scale(1, 1); 
            transition: all 0.5s ease;
        }
        .hamburger-top, 
        .hamburger-bottom {
            content: "";
            display: block; 
        }
        .hamburger-top {
            top: -10px;
        }
        .hamburger-bottom {
            bottom: -10px; 
        }
        /*
            each line - active
        */
        // &.opened {
        //     opacity:0.6;
        // }
        &.opened .hamburger-middle, 
        &.opened .hamburger-top, 
        &.opened .hamburger-bottom {
            width:21px;
            margin-right:8px;
            margin-left:2px;
        }
        &.opened .hamburger-top {
            transform: translate3d(-8px, 0, 0) rotate(-45deg) scale(0.8, 1); 
            top: -5px; 
        }
        &.opened .hamburger-bottom {
            transform: translate3d(-8px, 0, 0) rotate(45deg) scale(0.8, 1); 
            bottom: -5px; 
        }
 `;

module.exports = Styled;


