import React from 'react';
import Box from '@src/components/box/Box';
import * as Styled from './TopStyled';

class Top extends React.PureComponent {

	render() {

		if (window.device) {

			var DeviceInfo = 
				<Box box={{title:"New Luxul Product", theme:"global"}}>
					<div className="form">
						<p>Model #: {window.device.info.model}</p>
						<p>Firmware #: {window.device.info.firmware}</p>
					</div>
				</Box>;
			
			var Details = 
				<Styled.Details onClick={()=>{ window.state.side = {Box:DeviceInfo}; }}>
					<span className="link">
						<span className="title" style={{textIndent:"-1.5px",paddingTop:"0.1rem"}}>{/*<span className="fontIcon icon-carat_down" style={{color:"rgba(255, 255, 255, .3)"}} /> */}{window.device.info.model}</span>
						<span className="text" style={{textIndent:"-1px"}}>Firmware: {window.device.info.firmware}</span>
					</span>
					<span className="fontIcon icon-navlink_info"></span>
				</Styled.Details>;

		}

		return (
			<Styled.Top>
				<div>

					<Styled.Hamburger id="Hamburger" onClick={(event)=>{ if (window.state.nav.opened) { document.getElementById("Hamburger").classList.remove('opened'); } else { document.getElementById("Hamburger").classList.add('opened'); } window.state.nav={opened:(!window.state.nav.opened)} }}>
						<div className="hamburger-icon">
							<div className="hamburger-centered">
								<div className="hamburger-top"></div>
								<div className="hamburger-middle"></div>
								<div className="hamburger-bottom"></div>
							</div>
						</div>
					</Styled.Hamburger>

					<Styled.Logo onClick={()=>{ this.props.router.push('/login'); }}>
						<img src="/branding/logo/logo_white.svg" alt="LUXUL" />
					</Styled.Logo>
					
					{Details || null}
				
				</div>
			</Styled.Top>
		)
	}
}
export default Top;


