// only local data
class Nav extends React.PureComponent {

	render() {

		const { router } = this.props;
		const pathname = router.location.pathname;
		const NavLinks = [];

		if (window.device && window.device.routes.length) {
			for (let i = 0; i < window.device.routes.length; i++) {

				const pageLink = window.device.routes[i];
				const pageTabs = pageLink.children ||[];
				const NavTabs = [];
				var isPageSelected = pathname === pageLink.url;

				// tabs
				for (let t = 0; t < pageTabs.length; t++) {
					if (pageTabs[t] && !pageTabs[t].hidden) {
						let tab = pageTabs[t];
						var isTabSelected = pathname === tab.url;
						if (isTabSelected) {
							isPageSelected = true;
						}

						NavTabs.push(
							<div key={ "NavTabs"+t } className={ "tab link" + (isTabSelected ? ' selected' : '') } onClick={function(){ router.push( tab.url );  }}>
								<span>{tab.tabTitle || tab.title}</span>
							</div>
						);
					}
				}

				// links
				const isHaveTabs = NavTabs.length>1;
				NavLinks.push(
					<div key={"Nav_"+pageLink.url} ref={"Nav_"+pageLink.url} className={ 'navItem link' + (isPageSelected ? ' selected opened' : '') + (isHaveTabs ? ' withTabs' : '') } /*onMouseEnter={()=>{ this.refs["Nav_"+pageLink.url].classList.add("opened"); }} onMouseLeave={()=>{ this.refs["Nav_"+pageLink.url].classList.remove("opened"); }}*/>
						<div className="navItemTitle">

							<div className={ "link " + ((isPageSelected) && " navItemTitleFirst") } onClick={ (e)=>{ if (!isHaveTabs && pageLink.url){ router.push(pageLink.url); } else { this.refs["Nav_"+pageLink.url].classList.toggle("opened"); } e.preventDefault(); } }>
								
								<span className="fontIcon icon-navlink_plus"></span>
								<span className="fontIcon icon-navlink_minus"></span>
								<span className="fontIcon icon-navlink_dot"></span>
								
								<span>{pageLink.tabTitle || pageLink.title}</span>
								
								{/*(isHaveTabs && isPageSelected) && 
									<span className="colon fontIcon icon-carat_down"></span>
								*/}

							</div>

							{isHaveTabs &&
								<div className="nav1Tabs">
									{NavTabs}
								</div>
							}

						</div>
					</div>
				);

			};
		}

		// ok
		return (
			<div className="pages">
			<div className={"nav  nav_left"+(this.props.nav.opened?"":" closed")}>
				<div className="navItems">
					{NavLinks}
				</div>
			</div>
			</div>
		)
	}
}




// connect global data
// when {window.state.nav} changes, update {this.state.nav}
class NavConnected extends React.PureComponent {
	constructor(){
		super();
		this.state = {};
		this.state.nav = window.state.nav;
	};
	componentWillMount(){
		this._isMounted = true;

		window.state.watch("nav",(name,oldValue,value)=>{
			if (this._isMounted) {
				this.setState({[name]:value});
			}
			return value || {};
		});
	}
	componentWillUnmount(){
		this._isMounted = false;

		window.state.unwatch("nav");
	}
	render(){
		return (
			<Nav nav={this.state.nav} router={this.props.router} />
		)
	}
}



export default NavConnected;


